import React, { useEffect } from 'react';
import { useAuth } from "../context/AuthContext";
import { useNavigate } from 'react-router-dom';

function CustomerDashboard() {
  const { user } = useAuth();
  const navigate = useNavigate();

  useEffect(() => {
    console.log(' CustomerDashboard mounted');
    console.log(' Current user:', user);
    
    if (user) {
      console.log(' Redirecting to /customer/coupons');
      navigate('/customer/coupons', { replace: true });
    } else {
      console.log(' No user found, redirecting to login');
      navigate('/login', { replace: true });
    }
  }, [navigate, user]);

  console.log(' CustomerDashboard rendering');
  return (
    <div className="min-h-screen flex items-center justify-center">
      <div className="text-center">
        <div className="animate-spin rounded-full h-12 w-12 border-4 border-blue-400 border-t-transparent mx-auto mb-4"></div>
        <p className="text-gray-600">Loading customer dashboard...</p>
      </div>
    </div>
  );
}

export default CustomerDashboard;
