/**
 * Unified Toast & Alert Service for Admin Dashboard
 * 
 * Provides centralized notification management with consistent styling,
 * behavior, and messaging across all admin components.
 * 
 * Features:
 * - Standardized toast types (success, error, warning, info)
 * - Auto-dismiss with configurable duration
 * - Queue management for multiple toasts
 * - Responsive design with smooth animations
 * - Backend error mapping
 * - Consistent message formatting
 */

import React, { createContext, useContext, useState, useCallback, useEffect } from 'react';
import { CheckCircle, AlertCircle, AlertTriangle, Info, X } from 'lucide-react';

// Toast types with consistent styling
export const TOAST_TYPES = {
  SUCCESS: 'success',
  ERROR: 'error', 
  WARNING: 'warning',
  INFO: 'info'
};

// Default configuration
const DEFAULT_CONFIG = {
  duration: 4000, // 4 seconds
  position: 'top-right',
  maxToasts: 5,
  showCloseButton: true,
  pauseOnHover: true
};

// Message templates for consistency
export const MESSAGE_TEMPLATES = {
  // CRUD Operations
  CREATED: (item) => `${item} created successfully`,
  UPDATED: (item) => `${item} updated successfully`, 
  DELETED: (item) => `${item} deleted successfully`,
  STATUS_CHANGED: (item, status) => `${item} status updated to ${status}`,
  
  // Form Operations
  FORM_SAVED: 'Changes saved successfully',
  FORM_RESET: 'Form reset to default values',
  VALIDATION_ERROR: 'Please check the form for errors',
  
  // Data Operations
  DATA_LOADED: 'Data loaded successfully',
  DATA_REFRESHED: 'Data refreshed',
  EXPORT_SUCCESS: 'Data exported successfully',
  IMPORT_SUCCESS: 'Data imported successfully',
  
  // Authentication & Access
  ACCESS_DENIED: 'Access denied. Insufficient permissions',
  SESSION_EXPIRED: 'Session expired. Please log in again',
  UNAUTHORIZED: 'Unauthorized action',
  
  // Network & API
  NETWORK_ERROR: 'Network error. Please check your connection',
  SERVER_ERROR: 'Server error. Please try again later',
  API_ERROR: 'API request failed. Please try again',
  TIMEOUT_ERROR: 'Request timeout. Please try again',
  
  // Generic Messages
  OPERATION_SUCCESS: 'Operation completed successfully',
  OPERATION_FAILED: 'Operation failed. Please try again',
  PROCESSING: 'Processing your request...',
  PLEASE_WAIT: 'Please wait while we process your request'
};

// Backend error code mapping
export const ERROR_CODE_MAPPING = {
  400: { type: TOAST_TYPES.WARNING, message: 'Invalid request. Please check your input' },
  401: { type: TOAST_TYPES.ERROR, message: MESSAGE_TEMPLATES.UNAUTHORIZED },
  403: { type: TOAST_TYPES.ERROR, message: MESSAGE_TEMPLATES.ACCESS_DENIED },
  404: { type: TOAST_TYPES.WARNING, message: 'Resource not found' },
  409: { type: TOAST_TYPES.WARNING, message: 'Conflict detected. Please refresh and try again' },
  422: { type: TOAST_TYPES.WARNING, message: MESSAGE_TEMPLATES.VALIDATION_ERROR },
  429: { type: TOAST_TYPES.WARNING, message: 'Too many requests. Please wait and try again' },
  500: { type: TOAST_TYPES.ERROR, message: MESSAGE_TEMPLATES.SERVER_ERROR },
  502: { type: TOAST_TYPES.ERROR, message: 'Service temporarily unavailable' },
  503: { type: TOAST_TYPES.ERROR, message: 'Service maintenance in progress' },
  504: { type: TOAST_TYPES.ERROR, message: MESSAGE_TEMPLATES.TIMEOUT_ERROR }
};

// Toast Context
const ToastContext = createContext();

// Custom hook to use toast service
export const useToast = () => {
  const context = useContext(ToastContext);
  if (!context) {
    throw new Error('useToast must be used within a ToastProvider');
  }
  return context;
};

// Individual Toast Component
const Toast = ({ id, type, title, message, duration, onClose, showCloseButton, pauseOnHover }) => {
  const [isVisible, setIsVisible] = useState(false);
  const [isPaused, setIsPaused] = useState(false);

  useEffect(() => {
    // Trigger entrance animation
    const timer = setTimeout(() => setIsVisible(true), 10);
    return () => clearTimeout(timer);
  }, []);

  useEffect(() => {
    if (!duration || isPaused) return;

    const timer = setTimeout(() => {
      handleClose();
    }, duration);

    return () => clearTimeout(timer);
  }, [duration, isPaused]);

  const handleClose = () => {
    setIsVisible(false);
    setTimeout(() => onClose(id), 300); // Wait for exit animation
  };

  const getToastConfig = () => {
    switch (type) {
      case TOAST_TYPES.SUCCESS:
        return {
          icon: CheckCircle,
          bgColor: 'bg-green-500/10',
          borderColor: 'border-green-500/20',
          iconColor: 'text-green-400',
          titleColor: 'text-green-300',
          messageColor: 'text-green-200'
        };
      case TOAST_TYPES.ERROR:
        return {
          icon: AlertCircle,
          bgColor: 'bg-red-500/10',
          borderColor: 'border-red-500/20',
          iconColor: 'text-red-400',
          titleColor: 'text-red-300',
          messageColor: 'text-red-200'
        };
      case TOAST_TYPES.WARNING:
        return {
          icon: AlertTriangle,
          bgColor: 'bg-yellow-500/10',
          borderColor: 'border-yellow-500/20',
          iconColor: 'text-yellow-400',
          titleColor: 'text-yellow-300',
          messageColor: 'text-yellow-200'
        };
      case TOAST_TYPES.INFO:
      default:
        return {
          icon: Info,
          bgColor: 'bg-blue-500/10',
          borderColor: 'border-blue-500/20',
          iconColor: 'text-blue-400',
          titleColor: 'text-blue-300',
          messageColor: 'text-blue-200'
        };
    }
  };

  const config = getToastConfig();
  const IconComponent = config.icon;

  return (
    <div
      className={`
        transform transition-all duration-300 ease-in-out backdrop-blur-sm
        ${isVisible ? 'translate-x-0 opacity-100' : 'translate-x-full opacity-0'}
        ${config.bgColor} ${config.borderColor}
        border rounded-lg shadow-lg p-4 mb-3 min-w-[320px] max-w-[480px]
      `}
      onMouseEnter={() => pauseOnHover && setIsPaused(true)}
      onMouseLeave={() => pauseOnHover && setIsPaused(false)}
    >
      <div className="flex items-start space-x-3">
        <IconComponent className={`w-5 h-5 ${config.iconColor} flex-shrink-0 mt-0.5`} />
        
        <div className="flex-1 min-w-0">
          {title && (
            <h4 className={`font-semibold text-sm ${config.titleColor} mb-1`}>
              {title}
            </h4>
          )}
          <p className={`text-sm ${config.messageColor} leading-relaxed`}>
            {message}
          </p>
        </div>

        {showCloseButton && (
          <button
            onClick={handleClose}
            className={`${config.iconColor} hover:opacity-70 transition-opacity flex-shrink-0`}
            aria-label="Close notification"
          >
            <X className="w-4 h-4" />
          </button>
        )}
      </div>
    </div>
  );
};

// Toast Container Component
const ToastContainer = ({ toasts, position, onClose, config }) => {
  const getPositionClasses = () => {
    switch (position) {
      case 'top-left':
        return 'top-6 left-6';
      case 'top-center':
        return 'top-6 left-1/2 transform -translate-x-1/2';
      case 'top-right':
      default:
        return 'top-6 right-6';
      case 'bottom-left':
        return 'bottom-6 left-6';
      case 'bottom-center':
        return 'bottom-6 left-1/2 transform -translate-x-1/2';
      case 'bottom-right':
        return 'bottom-6 right-6';
    }
  };

  if (toasts.length === 0) return null;

  return (
    <div className={`fixed z-50 ${getPositionClasses()}`}>
      <div className="space-y-2">
        {toasts.map((toast) => (
          <Toast
            key={toast.id}
            {...toast}
            onClose={onClose}
            showCloseButton={config.showCloseButton}
            pauseOnHover={config.pauseOnHover}
          />
        ))}
      </div>
    </div>
  );
};

// Toast Provider Component
export const ToastProvider = ({ children, config = {} }) => {
  const [toasts, setToasts] = useState([]);
  const mergedConfig = { ...DEFAULT_CONFIG, ...config };

  const addToast = useCallback((toast) => {
    const id = Date.now() + Math.random();
    const newToast = {
      id,
      duration: mergedConfig.duration,
      ...toast
    };

    setToasts(prev => {
      const updated = [...prev, newToast];
      // Limit number of toasts
      if (updated.length > mergedConfig.maxToasts) {
        return updated.slice(-mergedConfig.maxToasts);
      }
      return updated;
    });

    return id;
  }, [mergedConfig]);

  const removeToast = useCallback((id) => {
    setToasts(prev => prev.filter(toast => toast.id !== id));
  }, []);

  const clearAllToasts = useCallback(() => {
    setToasts([]);
  }, []);

  // Convenience methods for different toast types
  const showSuccess = useCallback((message, title = 'Success') => {
    return addToast({ type: TOAST_TYPES.SUCCESS, title, message });
  }, [addToast]);

  const showError = useCallback((message, title = 'Error') => {
    return addToast({ type: TOAST_TYPES.ERROR, title, message });
  }, [addToast]);

  const showWarning = useCallback((message, title = 'Warning') => {
    return addToast({ type: TOAST_TYPES.WARNING, title, message });
  }, [addToast]);

  const showInfo = useCallback((message, title = 'Info') => {
    return addToast({ type: TOAST_TYPES.INFO, title, message });
  }, [addToast]);

  // Handle backend errors with automatic mapping
  const handleApiError = useCallback((error, fallbackMessage = MESSAGE_TEMPLATES.OPERATION_FAILED) => {
    let toastConfig = { type: TOAST_TYPES.ERROR, title: 'Error' };
    
    if (error?.response?.status) {
      const statusCode = error.response.status;
      const mapping = ERROR_CODE_MAPPING[statusCode];
      if (mapping) {
        toastConfig = { ...toastConfig, ...mapping };
      }
    } else if (error?.message) {
      toastConfig.message = error.message;
    } else {
      toastConfig.message = fallbackMessage;
    }

    return addToast(toastConfig);
  }, [addToast]);

  // Handle successful operations with templates
  const handleSuccess = useCallback((operation, item = '') => {
    const template = MESSAGE_TEMPLATES[operation];
    const message = typeof template === 'function' ? template(item) : template || operation;
    return showSuccess(message);
  }, [showSuccess]);

  const value = {
    // Core methods
    addToast,
    removeToast,
    clearAllToasts,
    
    // Convenience methods
    showSuccess,
    showError,
    showWarning,
    showInfo,
    
    // Specialized methods
    handleApiError,
    handleSuccess,
    
    // State
    toasts,
    config: mergedConfig
  };

  return (
    <ToastContext.Provider value={value}>
      {children}
      <ToastContainer
        toasts={toasts}
        position={mergedConfig.position}
        onClose={removeToast}
        config={mergedConfig}
      />
    </ToastContext.Provider>
  );
};

// Export utility functions for direct use
export const createToastService = (config = {}) => {
  const mergedConfig = { ...DEFAULT_CONFIG, ...config };
  
  return {
    success: (message, title = 'Success') => ({ type: TOAST_TYPES.SUCCESS, title, message }),
    error: (message, title = 'Error') => ({ type: TOAST_TYPES.ERROR, title, message }),
    warning: (message, title = 'Warning') => ({ type: TOAST_TYPES.WARNING, title, message }),
    info: (message, title = 'Info') => ({ type: TOAST_TYPES.INFO, title, message }),
    
    // Template helpers
    templates: MESSAGE_TEMPLATES,
    mapApiError: (error) => {
      if (error?.response?.status) {
        return ERROR_CODE_MAPPING[error.response.status] || { 
          type: TOAST_TYPES.ERROR, 
          message: MESSAGE_TEMPLATES.OPERATION_FAILED 
        };
      }
      return { type: TOAST_TYPES.ERROR, message: error?.message || MESSAGE_TEMPLATES.OPERATION_FAILED };
    }
  };
};

export default ToastProvider;
